import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
//Ant Design Vue
import './core/demand-loading'

//AOS动画
import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init({
  once: true, // 只展示一次动画
  offset: 50,
  delay: 100,
  duration: 1000,
})
//自定义样式
import './styles/main.scss';
import './styles/typer.scss';

Vue.config.productionTip = false


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
