import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";
import json5 from "json5";

Vue.use(Vuex)

const state = {
    website: "",
    social: {},
    modules: [],
  }


export default new Vuex.Store({
  state,
  mutations: {
    addToDataList(state, payload) {
      state.website = payload.website;
      state.social = payload.social;
      state.modules = payload.modules;
      // console.log(state.modules[1])
    }
  },
  actions: {
    addToMutation(context, payload) {
      axios.get('/data.json5').then( res => {
        // console.log(json5.parse(res.data))
        context.commit('addToDataList',json5.parse(res.data))
      })
      // context.commit('addToDataList', payload)
    }
  },
  getters: {
    // social(state) {
    //   // console.log(state)
    //   // return state.dataList[0].social || {};
    //   // console.log(state)
    //   return state.social.github || {};
    // }
    menu(state) {
      // console.log(this.$store.state.modules[0])
      return state.modules
    },
    getBlog(state) {
      return state.modules[1]
    },
    getModule(state) {
      return (id) => {
        let data = state.modules
        return data.find(item => item.id == id)
      }
    }
  }
})
