<template>
  <div class="content" v-if="newBlog">
    <ModuleHeader :title="newBlog.title" :subtitle="newBlog.subtitle"/>
    <a-timeline>
      <a-timeline-item data-aos="fade-in"
                       v-for="note in newBlog.notes">
        <a-card class="notes" :bordered="true" style="width: 100%">
          <template slot="title">
            <h1 class="title">{{note.title}}</h1>
            <span class="sub-title">{{note.date}}</span>
          </template>
          <vue-markdown>{{note.md}}</vue-markdown>
        </a-card>
      </a-timeline-item>
    </a-timeline>
  </div>
</template>

<script>
  import ModuleHeader from "./ModuleHeader";
  import VueMarkdown from 'vue-markdown'
  export default {
    name: "Blog",
    components: {
      ModuleHeader,
      VueMarkdown
    },
    computed: {
      newBlog() {
        return this.$store.getters.getModule('Blog')
      }
    }
  }
</script>

<style scoped lang="scss">
  @import '../../styles/variable';


  .notes {
    .title {
      width: 100%;
      font-size: 1rem;
      /*overflow: scroll;*/
      margin: 0;
      font-weight: bold;

      /*text-overflow: ellipsis;*/

    }

    .sub-title {
      width: 100%;
      font-size: .5rem;
      display: block;
      margin-top: .5rem;
    }
  }
</style>