<template>
  <a-layout>
    <a-layout>
      <!-- 菜单 -->
      <a-layout-sider class="layout-sider" width="320">
        <a-affix><Menu/></a-affix>
      </a-layout-sider>
      <!-- 正文部分 -->
      <a-layout class="layout-content">
        <!-- 小屏侧边栏抽屉按钮 -->
        <a-affix>
          <a-button :class="{'sider-menu-trigger': true,
                     'drawer-closed': !menuDrawerVisible,
                     'drawer-open': menuDrawerVisible}"
                    shape="circle" size="large"
                    :icon="menuDrawerVisible ? 'arrow-left' : 'bars'"
                    @click="toggleMenuDrawer">
          </a-button>
        </a-affix>
        <!-- 正文锚点 -->
<!--        <a-layout-content><div id="anchor-next"></div></a-layout-content>-->
        <!-- 根据配置动态模块的内容和顺序 -->
        <a-layout-content>
          <Blog v-if="isBlog"/>
          <router-view/>
        </a-layout-content>
        <!-- 页脚 -->
        <a-layout-footer>
          <Footer/>
        </a-layout-footer>
      </a-layout>
    </a-layout>

    <!-- 小屏侧边栏抽屉 -->
    <a-drawer placement="left"
              :closable="false"
              :visible="menuDrawerVisible"
              @close="onMenuDrawerClose">
      <Menu @menuClick="onMenuDrawerClose" />
    </a-drawer>
  </a-layout>
</template>

<script>
// @ is an alias to /src
import Menu from "../components/Menu"
import Blog from "../components/content/Blog"
import Footer from "../components/Footer";

export default {
  name: 'Home',
  components: {
    Menu,
    Blog,
    Footer
  },
  data(){
    return {
      menuDrawerVisible: false,
    }
  },
  computed: {
    isBlog(){
      return this.$route.path == '/home'  ? true : false
    }
  },
  methods: {
    toggleMenuDrawer(){
      this.menuDrawerVisible = !this.menuDrawerVisible
    },
    onMenuDrawerClose() {
      this.menuDrawerVisible = false;
    }
  },
  // updated() {
  //   console.log(this.$route)
  //   console.log(this.$router)
  // }
}
</script>

