<template>
    <a-layout class="layout-menu"  data-aos="slide-right">
      <!-- 头像 -->
      <div class="header">
            <span class="avatar ant-avatar ant-avatar-circle ant-avatar-image">
                <img draggable="false" src="../assets/avatar.jpg">
            </span>
        <span>A2ayak</span>
      </div>
      <!-- 菜单 -->
      <a-layout-content class="menu">
        <a-menu>
          <!-- 根据配置动态模块的内容和顺序 -->
          <a-menu-item v-for="(item,index) in menus" :key="item.id"
                       @click="switchModule(item.path)">
            <a-icon :type="item.icon"/>
            <a>{{item.name}}</a>
          </a-menu-item>
        </a-menu>
      </a-layout-content>
      <!-- 菜单页脚 -->
      <a-layout-footer class="footer">
        <!-- 社交 -->
        <a-row type="flex" justify="center" align="middle" :gutter="2">
          <a-col span="24"><Social/></a-col>
        </a-row>
        <!-- 版权 -->
        <a-row type="flex" justify="center" align="middle" :gutter="2">
          <a-col span="24"><Copyrights/></a-col>
        </a-row>
      </a-layout-footer>
    </a-layout>
</template>

<script>
  import Copyrights from "./Copyrights";
  import Social from "./Social";
  import { mapGetters } from "vuex"
  export default {
    name: "Menu",
    components: {
      Copyrights,
      Social
    },
    computed: {
      menus() {
        return this.$store.getters.menu
      }
    },
    methods: {
      switchModule(path) {
        this.$router.push(path)
      }
    }
  }
</script>

<style scoped lang="scss">
  @import '../styles/variable';

  .layout-menu {
    width: 100%;
    height: 100vh;
    text-align: center;
    background-color: white;

    .header, .menu, .footer {
      background-color: white !important;
    }

    .header {
      margin: 0 auto;
      padding: 2rem 0;
      .avatar {
        width: 80px;
        height: 80px;
        line-height: 80px;
      }
      span {
        display: block;
        margin-top: .5rem;
        font-size: 1.4em;
        font-weight: 500;
        color: $--color-gray;
      }
    }

    .menu {
      * {
        font-size: 1.05em;
      }

      a {
        display: inline;
      }
    }

    .footer {
      padding: 10px;
      margin-bottom: 1rem;
      * {
        color: $--color-gray;
      }

      & > div {
        margin: .5rem auto;
      }
    }
  }
</style>